import config from '../config';
import React, { useEffect } from 'react'

import './Video.css';

function Video() {
    useEffect(() => {
        console.log('xxx');
    }, [])
    const origin = encodeURIComponent(document.location.origin);
    const src = `${config.youtubeVideoURL}&rel=0&showinfo=0&modestbranding=1&&iv_load_policy=3&enablejsapi=1&disablekb=1&hd=0&autoplay=1&mute=0&rel=0&autoplay=1&origin=${origin}&controls=1&enablejsapi=1`;


    return <div className="Video">
        <iframe
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
            allowFullScreen={true}
            src={src}
            title="YouTube video player"
        >
        </iframe>
    </div>;
}

export default Video;